import { createStore } from 'vuex';
import axios from 'axios';
import UserPool from '@/aws/UserPool.js';

const store = createStore({
  state: {
    posts: [],
    user: null,
  },
  getters: {
    getUser: state => {
        return state.user;
    },
    posts: state => {
        return state.posts;
    },
    isAuthenticated: state => !!state.user,
  },
  mutations: {
    LOGOUT(state){
        state.user = null;
    },
    SET_USER(state, username){
        state.user = username
    },
    SET_ITEMS (state, posts) {
        state.posts = posts;
    }
  },
  actions: {
  async loadPosts ({ commit }) {
    try {
        const response = await axios.get('http://localhost:8080/test_hellolambda/all-messages');
        commit('SET_ITEMS', response.data);
      } catch (error) {
        alert("Failed to send request cause of :" + error);
        console.log(error);
    }
    },
  async LogOut({commit}){
      commit('LOGOUT');
    },
  async addPost (post) { 
    console.log(post);
    /*
      try {
          const response = await axios.post('test://localhost:8080/test_hellolambda/', post);
          console.log(response.data);
        } catch (error) {
          alert("Failed to send request cause of :" + error);
          console.log(error);
      }*/
    },
  connectUser ( userdata) {
    console.log(userdata);
    UserPool.login(userdata["username"], userdata["password"], [], null, (err, data) => {
        if (err) {
            console.log(err);
            return false;
        } else {
            console.log(data);
            return true;
        }
    });
    /*try {
      
        const response = await axios.post('test://localhost:8080/test_hellolambda/',userdata);
        commit('SET_USER', userdata["username"]);
        if (x) {
            commit('SET_USER', userdata["username"]);
            return x;
        } else {
            alert("Login failed");
            return x;
        }
      } catch (error) {
        alert("Failed to send request");
        console.log(error);
    }*/
    }
  },
});

export default store